.items img {
  height: auto;
  width: 100%;
}
.image-container {
  height: 380px;
  width: 100%;
  overflow: hidden;
}

.item-prices {
  display: flex;
  gap: 20px;
  padding-bottom: 20px;
}
.item-prices-new {
  margin-left: 5px;
  color: rgb(12, 12, 12);
  font-size: 18px;
  font-weight: 600;
}
.item-prices-old {
  color: rgb(13, 14, 13);
  font-size: 18px;
  font-weight: 500;
  text-decoration-line: line-through;
}
.items:hover {
  transform: scale(1.05);
  transition: 0.3s;
}
